import { Button } from "@/components/ui/button";
import { sanity } from "@/lib/sanity";
import router from "next/dist/client/router";
import Image from "next/image";

const profileQuery = `*[_type == "user"][0] {
  _id,
  name,
  email,
  phone,
  company,
  "avatar": avatar.asset->url
}`;

export default async function ProfilePage() {
  const profile = await sanity.fetch(profileQuery);

  if (!profile) {
    return <div className="p-6">No profile found</div>;
  }

  return (
    <div className="max-w-3xl mx-auto p-6">
      <h1 className="text-2xl font-bold mb-6">Profile Details</h1>

      <div className="bg-white shadow rounded-xl p-6 space-y-4">
        {/* Avatar */}
        {profile.avatar && (
          <Image
            src={profile.avatar}
            alt="profile"
            width={120}
            height={120}
            className="rounded-full"
          />
        )}

        <p>
          <strong>Name:</strong> {profile.name}
        </p>

        <p>
          <strong>Email:</strong> {profile.email}
        </p>

        {profile.phone && (
          <p>
            <strong>Phone:</strong> {profile.phone}
          </p>
        )}

        {profile.company && (
          <p>
            <strong>Company:</strong> {profile.company}
          </p>
        )}
      </div>

      <Button variant="destructive" onClick={() => {
        localStorage.removeItem("user"); // remove login data
        router.push("/login"); // redirect to login page
      }}>
        Logout
      </Button>
    </div>
  );
}
